import React from 'react';

import { GalleryPreview } from './GalleryPreview';

function GalleryList({ props, type }) {
    return (
        props.map(prop => {
            return type === 'imgs' ?
                <GalleryPreview key={prop._id} img={prop} /> :
                <div key={prop._id} className="flex column gallery-preview-container">
                    <img src={require(`../../assets/artistsImg/${prop.imgId}.jpg`)} alt="" />
                    <div>{prop.name}</div>
                </div>
        })
    )
}

export { GalleryList };